
.subscribeForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.formInput {
  display: flex;
}

.subscribeAgreement {
  margin-top: 42px;
  display: flex;
  flex-direction: row;
}

.checkboxContainer {
  display: block;
  position: relative;
  cursor: pointer;
  padding-left: 35px;
  margin-bottom: 20px;
  font-size: 25px;
}

.checkboxContainer input {
  visibility: hidden;
  cursor: pointer;
}

.mark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #8534FF;
}

.checkboxContainer:hover input ~ .mark {
  background-color: purple;
}

.checkboxContainer input:checked ~ .mark {
  background-color: black;
}

.mark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkboxContainer input:checked ~ .mark:after {
  display: block;
}

.checkboxContainer .mark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

.checkboxText {
  font-family: 'Lato', serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #A1A1A1;
}

.formControl {
  box-sizing: border-box;
  height: 45px;
  width: 320px;
  border: 1px solid #EBEBEB;
  padding:10px;

  @media screen and (max-width: 992px) {
    height: 30px;
    width: 250px;
  }

  @media screen and (max-width: 576px) {
    width: 171px;
  }
}

.isInvalid {
  background-color: #f1abab;
}

.mailErrorText {
  font-family: "Poppins", serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 50px;
  text-align: center;
  color: #000000;
  position: absolute;
}

.subscribeButton {
  background-color: var(--button);
  cursor: pointer;
  outline: none;
  border: 0;

  &:active {
    outline: none;
  }

  &:focus {
    outline: none;
  }

  span {
    font-family: 'Poppins', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 22px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: white;
  }

  &.disabled {
    background-color: #6C6C6C;
  }
}
