@import '../../assets/fonts/fonts';
@import '../../App.scss';

.wrapper {
  margin: 0 auto;
  width: 1150px;

  @media screen and (max-width: 1280px) {
    width: 930px;
  }

  @media screen and (max-width: 992px) {
    width: 768px;
  }

  @media screen and (max-width: 768px) {
    width: 567px;
  }

  @media screen and (max-width: 576px) {
    width: 95%;
  }
}

::-webkit-scrollbar {
  width: 0;
}

.home {
  background-color: var(--background-color);
}
